import Vue from 'vue'
import VueRouter from 'vue-router'
import DinoparkWireframe from '../components/DinoparkWireframe.vue'
import Character from '../components/Character.vue'
import Map from '../components/Map.vue'
import Dinosaurs from '../components/Dinosaurs.vue'
import Spinosaurus from '../components/Spinosaurus.vue'
import Combat from '../components/Combat.vue'
import Catch from '../components/Catch.vue'
import Info from '../components/Info.vue'
import Quiz from '../components/Quiz.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'DinoparkWireframe',
    component: DinoparkWireframe
  },{
    path: '/character',
    name: 'Character',
    component: Character
  },
  {
    path: '/map',
    name: 'Map',
    component: Map
  },
  {
    path: '/dinosaurs',
    name: 'Dinosaurs',
    component: Dinosaurs
  },
  {
    path: '/dinosaurs/spinosaurus',
    name: 'Spinosaurus',
    component: Spinosaurus
  },
  {
    path: '/combat',
    name: 'Combat',
    component: Combat
  },
  {
    path: '/catch',
    name: 'Catch',
    component: Catch
  },
  {
    path: '/dinosaurs/spinosaurus/info',
    name: 'Info',
    component: Info
  },
  {
    path: '/dinosaurs/spinosaurus/quiz',
    name: 'Quiz',
    component: Quiz
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
